.button {
  display: flex;
  /* padding-left: 20px; */
  justify-content: center;
  align-items: center;
  gap: 15px;
  border: none;
  outline: none;

  width: 42vw;
  height: 39px;
  flex-shrink: 0;

  border-radius: 8px;
  background: #f2f2f2;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);

  font-family: Roboto Flex;
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -0.165px;
}

.button > img {
  scale: 1.1;
}
