.box {
  height: 16vh;
  width: 42vw;
}

.box .card {
  height: 16vh;
  width: 42vw;
  /* border: 1px solid #424242; */
}

.box .overlap-group {
  background-color: #ffffff;
  border-radius: 11px;
  box-shadow: 0px 0px 20px #00000040;
  height: 16vh;
  position: relative;
  width: 42vw;
  display: flex;
}

.box .overlap {
  height: 75px;
  left: 1px;
  position: absolute;
  top: 1px;
  width: 42vw;
}

.box .rectangle {
  height: 8vh;
  left: 0px;
  object-fit: cover;
  position: absolute;
  top: 0px;
  border-radius: 10px;
  width: 41.35vw;

  -webkit-filter: blur(var(--blur-bg-2));
  -moz-filter: blur(var(--blur-bg-2));
  -o-filter: blur(var(--blur-bg-2));
  -ms-filter: blur(var(--blur-bg-2));
  filter: blur(var(--blur-bg-2));
}

.box .group {
  /* background: linear-gradient(
    180deg,
    rgb(98.55, 170.8, 223.12) 0%,
    rgb(233.75, 81.81, 227.67) 100%
  ); */
  border-radius: 10px;
  height: 55px;
  left: calc((42vw / 2) - 27.5px);
  position: absolute;
  top: calc(8vh - 35px);
  width: 55px;
}

.box .icon-instagram-fill {
  height: 39px;
  left: 8px;
  position: absolute;
  top: 8px;
  width: 39px;
}

.box .text-wrapper {
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 0;
  color: #424242;
  display: -webkit-box;
  font-size: 13px;
  font-weight: 400;
  left: calc((42vw / 2) - 62.5px);
  letter-spacing: -0.17px;
  line-height: normal;
  overflow: hidden;
  position: absolute;
  text-align: center;
  text-overflow: ellipsis;
  top: calc((16vh / 2) + 3.5vh);
  white-space: nowrap;
  width: 125px;
}
