@import url("https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100;8..144,200;8..144,300;8..144,400;8..144,500;8..144,600;8..144,700;8..144,800;8..144,900;8..144,1000&display=swap");

* {
  font-family: Roboto Flex;
  font-feature-settings: "clig" off, "liga" off;
  letter-spacing: -0.165px;
}

:root {
  --white: #fff;
  --blur-bg: 3px;
  --blur-bg-2: 0.8px;
}

h2 {
  color: var(--white);
  margin: 15px;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

h3 {
  color: var(--white);
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: -0.165px;

  margin-bottom: 6px;
}

.BgImage {
  position: fixed;
  left: -10px;
  right: 0;
  top: -10px;
  z-index: 1;
  display: block;
  height: 110vh;
  width: 110vw;

  background: url("https://images.pexels.com/photos/1761279/pexels-photo-1761279.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: blur(var(--blur-bg));
  -moz-filter: blur(var(--blur-bg));
  -o-filter: blur(var(--blur-bg));
  -ms-filter: blur(var(--blur-bg));
  filter: blur(var(--blur-bg));
}

.App {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 9999;

  height: 100vh;
  width: 100vw;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Head {
  /* background-color: antiquewhite; */
  height: 40vh;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.ProfileDescription {
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: var(--white);
}

.ProfileImage {
  width: 110px;
  height: 110px;
  object-fit: cover;

  border-radius: 20px;
}

.ProfileBtns {
  width: 100%;
  margin-top: 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
}

.Content {
  min-height: 60vh;
  width: 95vw;
  max-width: 95vw;
  margin-left: 5vw;
  /* background-color: aliceblue; */
  display: flex;
  flex-direction: column;
}

.CardsCont::-webkit-scrollbar {
  display: none;
}

.CardsCont {
  display: flex;
  gap: 20px;
  padding-bottom: 40px;
  padding-right: 25px;
  overflow-x: auto;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.section {
  min-width: 100%;
}
